import {Component, Input, ChangeDetectionStrategy} from '@angular/core';


export class NglDatepickerWeekdays {

   dayNamesShort: string[];
   dayNamesLong: string[];

  weekdays: any[] = [];

  ngOnChanges() {
    for (let i = 0; i < 7; i++) {
      this.weekdays.push({
        id: `weekday-${i}`,
        label: this.dayNamesShort[i],
        title: this.dayNamesLong[i],
      });
    }
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'tr[nglWeekdays]',
  template: `
    <th *ngFor="let day of weekdays" [id]="day.id" scope="col"><abbr [title]="day.title">{{day.label}}</abbr></th>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush,
}, ] },
];
/** @nocollapse */
static ctorParameters: ({type: any, decorators?: DecoratorInvocation[]}|null)[] = [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'dayNamesShort': [{ type: Input },],
'dayNamesLong': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
